@import url('https://fonts.googleapis.com/css2?family=Courier+Prime&display=swap');
@import url("https://fonts.googleapis.com/css?family=Roboto:400,300,600,400italic");

/* font-family: 'Courier Prime', monospace; */

*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  overflow-x: hidden;
}

.header{
  /* width: 100vh; */
  height: 100vh;
  background-image: url("../public/img/bg\ 1.jpg");
  background-repeat: no-repeat;
  background-size: cover;

}


.header_div-te{
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
}

.h1{
  color:#ffff;
  font-weight: 200;
  font-size:8rem;
  letter-spacing: 4px;
  cursor:all-scroll


}

.spanh1::after{
 content: "_";
 font-weight: 700;
  animation:blinking 0.7s infinite;
}

.h5{
  font-size: 2rem;
  color: #ffff;
  position: absolute;
  font-weight: 100;
  letter-spacing: 4px;
  bottom: 0;
  margin-bottom: 40px;
  padding-bottom: 10px;
  transition: all 0.2s ease-in-out;

}

.spanh5{
  color:#f07b3f;
  font-weight: bold;

}


.h5:hover{
  cursor:all-scroll;
  border-bottom: 2px solid #f07b3f;
}

/* sec_1 */

.sec_1 , .Aboutme{
  background-color: #03111e;
  width: 100vw;
  /* height: 100vh; */
  position: relative;
}


.sec_1 h1 , .Aboutme h1{
    font-family: 'Courier Prime', monospace;
    font-size: 4rem;
    color: #ffff;
    font-weight: 200;
    text-align: center;
    padding: 30px 0;
    letter-spacing: 4px;
  }

.card_men{
  width: 27%;
  height: 300px;
  border: 2px solid #66d9ef;
  overflow: hidden;
  border-radius: 17px;
  box-shadow: -3px 2px 20px 0px #66d8ef66;
}

.card_men img{
  width: 100%;
  /* height: 100%; */
  
}

.card_men{
  transition: all 0.2s ease-in-out;

}

.card_men:hover{
   width: 25%;
   cursor: pointer;
}


/* U_c */

.U_c{
  width: 56%;
  /* height: 350px; */
  border: 1px solid #66d9ef;
  display: flex;
  justify-content: center;
  align-items: center;
  display: none;
}


.u_c1 , .u_c2{
  width: 50%;
  height: 100%;

}


.u_c1{
  overflow: hidden;
}
.u_c1 img{
  width: 100%;
  height: 100%;
}


.u_c2{
  background-color:  hsla(0,0%,60%,.438);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-family: 'Courier Prime', monospace;
}


.u_c2 h2{
  font-size: 22px;
  margin-bottom: 20px;
  color: #41423c;
  /* letter-spacinx; */
}

.u_c2 p{
  font-size: 16px;
  text-align: center;
  color: #ffff;
  margin-bottom: 40px;
  text-transform: capitalize;
  /* line-height: 18px; */

}

.u_c2 a{
  font-size: 16px;
  text-decoration: none;
  color: #272822;
  
}

.showe_upper_card{
  /* position: absolute; */
  top: 50%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  /* left: 50%; */
}




















/* Aboutme */

.master_about{
  width: 100%;
  display: flex;
}

.first{
  width: 70%;
  border: 4px solid #66d9ef;
  background-color:#272822;
  border-radius: 10px;
  font-size: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 50px;
  padding: 10px;
}

.secand{
  width: 25%;
  margin-left: 4%;
  margin-right: 1%;
  display: flex;
  align-items: center;
  border-radius: 50%;

}

.secand img{
  width: 95%;
  height: 85%;
  border-radius: 50%;
  filter: blur(20px);
  transition: all 2s ease-in-out;
  /* opacity: 0.1; */
}


.c_s_s{
  color: #66d9ef;
  font-size: 20px;
  font-weight: 100;
  font-family: monospace;
  margin: 11px 0px;

}

.div_c h2{
  color: #f92672;
  font-weight: 100;
font-family: monospace;
margin: 11px 0px;

}
.code_C{
    color: #fd971f;
    font-weight: 100;
  font-family: monospace;
  margin: 11px 0px;

}

.slace{
  color: #66d9ef;
  margin-left: -17px;
}

.btn{
  padding: 15px 25px;
  border-radius: 5px;
  border: 2px solid #66d9ef;
  background-color: #03111e;
  color: #fff;
  font-size: 18px;
  margin-left: 50%;
  transition: all 1s ease-in-out;

}

.btn:hover{
  background-color: #66d8ef7e;
  cursor: pointer;
}

.code{
  overflow: revert;
}

.slace{
  font-size: 23px;
}


